
.wrapper {
    width: 100%;
}

.wrapper .wrapper_content {
    margin-left: 1.5rem;
    margin-right: 1.5rem;
    padding-top: 3rem;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
}

@media (max-width:1024px) {
    .wrapper .wrapper_content{
        padding-top: 0;
    }
}

.wrapper .wrapper_content h1 {
    margin-bottom: 30px;
    font-size: 28px;
    color: var(--title-text-color);
}

.wrapper .wrapper_content p {
    color: var(--text-color);
    font-size: 18px;
    line-height: 38px;
    margin-bottom: 15px;
}

.wrapper .commentContent {
    margin-left: 1.5rem;
    margin-right: 1.5rem;
    margin-top: 3rem;
    max-width: 880px;
}