/* 必须使用两行 --- 来标记 */

@import "style";
@import "common";
@import "header";
@import "index";
@import "page";

:root {
    --background-color: #ffffff;
    --item-background-color: #fff;
    --title-text-color: #2a2f36;
    --text-color: #6c7a89;
    --border-color: #f7f5f5;
    --like-text-color: #82859B;
    --like-border-color: #E5E6EA;
    --heart-color: #C1C2CD;
}

@media (prefers-color-scheme: dark) {
    :root {
        --background-color: #171514;
        --item-background-color: #232222;
        --title-text-color: #efefef;
        --text-color: #b8b8b8;
        --border-color: #262626;
        --like-text-color: #82859B;
        --like-border-color: #82859B;
        --heart-color: #82859B;
    }
}