.link_item {
    width: 30%;
    max-width: 450px;
    padding-left: 30px;
    padding-top: 30px;
}

.link_item .content {
    height: 250px;
    border-radius: 8px;
    box-shadow: 0px 0px 40px 20px rgba(104, 103, 103, 0.05);
    background: var(--item-background-color);
    padding: 20px 25px;
	-webkit-transition: .2s linear;
	transition: .2s linear;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
   
}

.link_item .content:hover {
    box-shadow: 0px 0px 40px 20px rgba(104, 103, 103, 0.05);
    transform: translate3d(0px, -8px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg); 
    transform-style: preserve-3d;
    -webkit-transition: .2s linear;
	transition: .2s linear;
}

.link_item .content .logo {
    width: 58px;
    min-width: 58px;
    min-height: 58px;
    height: 58px;
    border-radius: 8px;
}


.link_item .content .like {
    display: flex;
    width: auto;
    height: auto;
    margin-top: -3px;
    text-align: center;
    padding: 4px 8px;
    color: var(--like-text-color);
    float: right;
    border: solid;
    font-size: 13px;
    border-radius: 5px;
    border-color: var(--like-border-color);
    border-width: 1px;
    cursor: pointer;
}

.link_item .content .like .heart {
    color: var(--heart-color);
}

.link_item .content .title {
    font-weight: 550;
    font-size: 19px;
    margin-top: 12px;
}

.link_item .content .title img {
    width: 18px;
    height: 18px;
}

.link_item .content .description {
    margin-top: 12px;
    color: var(--text-color);
    font-size: 13px;
    line-height: 20px;
}

.link_item .content .description a {
    color: var(--title-text-color);
    font-weight: 600;
    font-size: 14px;
}

.link_item .content .category {
    display: inline-block;
    overflow: visible;
    margin-top: 16px;
    margin-left: 0;
    margin-right: 3px;
    padding: 6px 14px;
    border-radius: 20px;
    background-image: linear-gradient(180deg,hsla(0,0%,100%,0.8),hsla(0,0%,100%,.7));
    font-weight: 400;
    font-size: 12px;
}

@media (max-width:1250px) {
    .link_item {
        width: 45%;
    }
}

@media (max-width:1024px) {
    .link_item {
        width: 48%;
        margin: 0 auto;
        padding-top: 30px;
        padding-left: 0;
    }
}

@media (max-width:600px) {
    .link_item {
        width: 100%;
        max-width: 560px;
        padding-left: 20px;
        padding-right: 20px;
    }
    
}

.pagination {
    margin:-60px auto 80px;
    border-radius: 8px;
    box-shadow: 0px 0px 40px 20px rgba(104, 103, 103, 0.05);
    background: var(--item-background-color);
    padding: 20px 25px;
    color: var(--text-color);
    display: block;
    width: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    text-align: center;
}